@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin content-padding-inline {
  padding-inline: 16px;

  @include on-tablet {
    padding-inline: 44px;
    max-width: 1200px;
    margin: 0 auto;
  }
}

.container {
  @include content-padding-inline;
}

@mixin page-grid {
  --columns: 2;

  display: grid;
  column-gap: 16px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 8;

    column-gap: 24px;
  }
}
